import VideoListWrap from "@/components/common/VideoListWrap";

export default {
  name: "",
  props: [""],
  data() {
    return {};
  },

  components: {
    VideoListWrap
  },

  computed: {
    contentSty() {
      return { height: document.documentElement.clientHeight - 50 + "px" };
    }
  },

  beforeMount() {},

  mounted() {},

  methods: {
    back() {
      this.$router.goBack();
    },
    releaseContent() {
      this.$router.push("/releasevideo");
    }
  },

  watch: {}
};



